@import url('https://fonts.googleapis.com/css2?family=Anek+Latin:wght@100;200;300&family=Arima:wght@100;200;300&family=Cinzel:wght@500&family=Inter:wght@500;600&family=Irish+Grover&family=Montserrat:wght@100;200&family=Roboto+Slab:wght@400;500;600&family=Roboto:ital,wght@0,500;0,700;1,300&display=swap');


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Anek Latin', sans-serif;
font-family: 'Arima', cursive;
font-family: 'Cinzel', serif;
font-family: 'Inter', sans-serif;
font-family: 'Irish Grover', cursive;
font-family: 'Montserrat', sans-serif;
font-family: 'Roboto', sans-serif;
font-family: 'Roboto Slab', serif;
  /* background-color: rgb(247, 212, 247); */
  display: flex;
  justify-content: center;
  height: 90vh;
  width: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container{
  /* border: 2px solid red; */
  text-align: center;
  margin: 1rem;
  height: 12rem;
  color:rgb(62, 62, 62);
}

.input{
  height: 2rem;
  width: 22rem;
  
}


.sec_container{

  display: flex;
  flex-direction: row;
 flex-wrap:wrap;
  justify-content: space-between;
 
  width: 100%;
}

.search{
  height: 2rem;
  border: none;
  margin: 1rem;
  width: 5rem;
  cursor: pointer;
}

.image{
 
    height: 15rem;
    width: 19rem;
    border-radius: 0.5rem;
   
}

.cards{
  /* border: 2px solid red; */
border-radius: 0.5rem;
  box-shadow: 5px 5px 10px  rgb(227, 223, 223);
  width:19rem;
  height: 21rem;
  text-align: center;
  color:grey;
  margin: 1rem;
}

h3{
  position: relative;
  bottom: 8%;
  padding: 0%;
  margin: 0%;
  
}

.headings{
  /* border: 2px solid red; */
  height: 5rem;
}


